/* --------- COMMON ----------------- */

body {
  min-height: 100vh;
  min-height: -webkit-fill-available;
}
html {
  height: -webkit-fill-available;
}

/* .alternate-section-1 {
  background-color: #f8f8ee;
} */

.section-heading {
  font-family: Chopin, cursive;
  /* font-size: 8vmax; */
  font-size: 8rem;
  text-align: center;
  padding: 1% 0%;
  font-weight: 600;
  color: #581d07;
}

/* @media (max-width: 450px) {
  .section-heading {
    font-size: 4rem;
  }
} */

*:focus {
  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none !important;
}

/* -------------- MAIN SECTION ----------------------- */

.main-section {
  background-image: url(/wedding-invitation-website/static/media/main-section-bg.19988ef3.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  /* background-attachment: fixed; */
  background-position: center;
  height: 100vh;
}

.layer {
  background-color: rgb(87, 0, 0, 0.55);
  height: 100%;
  width: 100%;
  /* padding-top: 12%;
  padding-bottom: 7%; */
  color: #fff;
  position: relative;
  z-index: 2;
}

.main-title {
  text-align: center;
  font-family: Chopin, cursive;
  font-size: 9rem;
  color: gold;
  /* padding-top: 32%; */
  font-weight: 800;
  position: relative;
  top: 52%;

  /* margin-top: 70%; */
}

.main-names-img {
  width: 90%;
  /* text-align: center; */
  position: relative;
  top: 60%;
  left: 5%;
}

.mobile-main-names-img {
  width: 75%;
  position: relative;
  top: 45%;
  left: 12.5%;
}
.and-img {
  width: 10%;
  display: block;
  margin: auto;
}

/* -------------- COUNTDOWN -------------------------- */

.countdown-container {
  background-color: #907018;
  z-index: 10;
  width: 60%;
  text-align: center;
  margin: auto;
  font-family: 'Red Rose';
  font-weight: 700;
  padding-bottom: 0.5%;
  position: absolute;
  bottom: -5vh;
  left: 20%;

  clear: both;
}

.count {
  font-size: 3vmax;
}

.count-unit {
  font-size: 1.2vmax;
}

/* ---------------- Invitation Section ------------------ */

/* .invitation-poster {
  width: 80%;
  display: block;
  margin: 0 auto;
} */
.invitation-section {
  background-color: #642711;
  /* background-color: #730400; */
  background-image: url('https://www.transparenttextures.com/patterns/45-degree-fabric-dark.png');
}

.invitation-container {
  /* padding: % 0; */
  /* min-height: 80vh; */
}

.invitation-container .container-fluid {
  /* padding: % 0; */
  height: inherit !important;
}

.invitation-container .col-md-7,
.invitation-container .col-md-5 {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.invitation-pic {
  /* max-width: 100%; */
  min-height: 100%;
  max-width: 100%;
  /* object-fit: contain; */
}

.content-container {
  padding: 10% 2%;
  text-align: center;
  background-color: #642711;
  /* background-color: #730400; */
  background-image: url('https://www.transparenttextures.com/patterns/45-degree-fabric-dark.png');
  min-height: 100%;
}

.invitation-names {
  font-family: Chopin;
  color: #f8ba5d;
  padding-top: 2.5%;
  padding-bottom: 2.4%;
