}
.assisting-text {
  text-transform: uppercase;
  color: #f1dba9;
  font-family: 'Montserrat', serif;
  font-weight: 600;
  font-size: 1.5rem;
  letter-spacing: 1px;
  padding-top: 2%;
  padding-bottom: 2%;
}

.invitation-date {
  font-size: 3.1rem;
  font-weight: 900;
  letter-spacing: 2px;
}

.lamp1 {
  width: 80%;
  height: 450px !important;
  /* margin-left: 20px; */
  /* display: block; */
  float: right;
}

.lamp2 {
  width: 80%;
  height: 450px !important;
  float: left;
}

.lamp-col {
  padding: 0px !important;
}

/* 576 - 768 */

/* --------------- INTRO SECTION ------------------------- */

.introduction-section {
  background-image: url(/wedding-invitation-website/static/media/image.0d6e0259.jpg);
  background-size: cover;
  background-repeat: no-repeat;

  background-position: top center;
  padding-bottom: 7%;
}

.intro-heading {
  padding-top: 3%;
  padding-bottom: 3%;
}

.intro-cards {
  text-align: center;
}

.card-profile {
  margin-top: 30px;
  text-align: center;
}

.card-profile {
  display: inline-block;
  position: relative;
  width: 90%;
  margin: auto;
  margin-bottom: 30px;
  border-radius: 6px;
  background-color: transparent !important;
  border-color: #86361a !important;
  border-width: 4px !important;
  box-shadow: 0 2px 2px 0 rgb(0 0 0 / 14%), 0 3px 1px -2px rgb(0 0 0 / 20%),
    0 1px 5px 0 rgb(0 0 0 / 12%);
  margin-top: 30px;
  text-align: center;
}

.card-profile .card-avatar {
  max-width: 220px;
  border-radius: 100%;
  margin: -50px auto 0;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  border-color: #86361a !important;
  border-width: 4px !important;
  border: 4px solid #86361a;
  /* box-shadow: 0 16px 38px -12px rgb(0 0 0 / 56%),
    0 4px 25px 0px rgb(0 0 0 / 12%), 0 8px 10px -5px rgb(0 0 0 / 20%); */
  transition-duration: 0.15s;
}

.card-profile .card-avatar:hover {
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

.card-profile img {
  width: 100%;
  height: auto;
}

/* .card-profileimg:hover {
  max-width: 225px;
} */

.card-profile .table {
  padding: 15px 30px;
}

.table {
  margin-bottom: 0px;
}

.card-caption {
  font-weight: 800;
  font-size: 2rem;
  font-family: 'Cassandra', cursive;
  color: #3d060d;
}
.card-caption,
.card-caption a {
  /* color: #333; */
  text-decoration: none;
}

.card-description {
  font-family: 'Montserrat';
  font-size: 1.1rem;
}

/* ------------------- PARENTS SECTION -------------------------- */

.parents-section {
  /* background: rgb(253, 250, 238);
  background: linear-gradient(
    180deg,
    rgba(253, 250, 238, 1) 0%,
    rgba(246, 218, 107, 1) 100%
  ); */
  background: rgb(253, 250, 238);
  background: linear-gradient(
    180deg,
    rgba(253, 250, 238, 1) 0%,
    rgba(181, 90, 55, 1) 100%
  );
  padding-bottom: 7%;
}

.parents-pic {
  border-radius: 100%;
  width: 70%;
  /* height: ; */
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.parents-block {
  /* border: black 2px solid; */
  border-radius: 6px;
  height: 90%;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  background-color: #fff;
  padding: 5%;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  text-align: justify;
}

/* --------FAMILY SECTION ------- */

.family-section {
  /* background: rgb(129, 102, 102);
  background: linear-gradient( */
  /* 180deg,
    rgba(255, 255, 255, 1) 0%,
    rgba(203, 64, 93, 1) 100%
  ); */
  /* background-color: #730400;

  background-image: url('https://www.transparenttextures.com/patterns/black-scales.png'); */
