  /* background-size: cover; */

  padding-bottom: 5%;
}

.family-section .carousel-indicators {
  height: 2px;
  /* padding-top: 1px; */
  position: absolute;
  bottom: 0 !important;
}

.family-section .carousel-indicators button {
  /* margin-top: 1%; */
  border: 0px transparent solid;
  height: 3px;
  width: 27px;

  /* border-radius: 100%; */
  margin-right: 1.2%;
  background-color: rgba(109, 84, 14, 0.7);
}

.family-section .carousel-indicators .active {
  background-color: #493705;
}

.bubble {
  height: auto;
  width: 100%;
  padding: 4% 5% 2%;
  /* padding-bottom: 2%; */
  min-height: 250px;
  /* min-width: 200px; */
  background: ivory;
  display: block;
  margin: 20px auto;
  border-radius: 20px;
  /* margin-top: 50px; */
  font-size: 1rem;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

.pointer {
  height: 40px;
  width: 40px;
  background: ivory;
  margin: 0 auto;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  /* border-radius: 0 0 0 12px; */
  /* margin-left: -14px; */
  position: relative;
  /* top: calc(0.5vh - 250px); */
}

.parents-pointer {
  border-radius: 0 0 12px 0;
  margin-top: -40px;
  left: calc(0.5vw - 31%);
}

.groom-sibling-pointer {
  margin-left: -20px;
  top: calc(0.5vh - 60%);
  border-radius: 0 0 0 12px;
}

.bride-sibling-pointer {
  margin-right: -15px;
  top: calc(0.5vh - 50%);
  border-radius: 0 12px 0 0;
}

.bubble-img-container {
  position: relative;
  top: 2%;
}

.bubble-img-container img {
  border-radius: 100%;
  width: 95%;
  display: block;
  position: relative;
  border: 6px white solid;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  /* left: 2vw; */
}

.sibling-msg .family-member-intro {
  text-align: center;
  /* position: relative;
  top: 18%; */
  font-weight: 700;
  color: #86361a;
  margin-top: 15%;
  /* font-size: 1.5rem; */
}

.sibling-msg .family-member-name {
  font-family: Cassandra, cursive;
  font-size: 1.2rem;
}

.sibling-msg .family-member-relation {
  font-size: 1rem;
  text-transform: uppercase;
}

.sibling-msg .col-md-7,
.sibling-msg .col-md-5 {
  padding: 0px !important;
}

.parents-msg .family-member-intro {
  text-align: left;
  position: relative;
  top: 35%;
  font-weight: 700;
  font-family: Cassandra, cursive;
  color: #86361a;
  font-size: 1.5rem;
}

.parents-msg .bubble {
  min-height: 292px !important;
}

.bubble-text {
  white-space: 'pre-wrap';
}

.read-more-btn {
  padding: 1%;
  color: black;
  text-decoration: underline;
  font-weight: 500;
  border: none !important;
  border-radius: 10px;
  background: none;

  /* background-color: #907018; */
}

