.venue-card {
  padding: 10%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 1%;
  margin-bottom: 20px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  /* min-height: 430px; */
  min-height: 100% !important;
  height: 100%;
  border-radius: 20px;
  transition: top ease 0.5s;
  position: relative;
  top: 0;
}

.venue-card:hover {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  top: -12px;
}
.venue-container {
  height: 100%;
  background-color: white !important;
  padding: 6%;
  border-radius: 5px;
  text-align: center;
}

.venue-card-icon {
  /* width: 20% !important; */
  height: 100px;
}

.venue-card-function {
  font-weight: bold;
  font-size: 1.3rem;
  color: #6b4018;
  font-family: Cassandra, cursive;
  margin-top: 10px;
}

.venue-card-details-container {
  font-size: 1.1rem;
  color: rgb(22, 21, 21);
  margin-top: 10px;
  font-weight: 500;
}

.venue-name {
  font-weight: 600;
  color: #925923;
  font-size: 1.1rem;
}

.venue-card-contact {
  /* color: #86361a !important; */
  font-size: 1.15rem;
  font-weight: bold;
}

.venue-card-contact a {
  color: #925923 !important;
}

.venue-card-map-button {
  background-color: transparent;
  color: #925923;
  border: #925923 solid 2px;
  font-weight: 500;
  padding: 2% 5% !important;
}

.venue-card-map-button:hover {
  background-color: #925923;
  color: #fff;
}

/* ----------------------- RSVP Section ------------------------- */

.rsvp-section {
  padding-top: 2%;
  padding-bottom: 7%;
  background-image: url(/wedding-invitation-website/static/media/rsvp-bg.5d0b63bf.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.rsvp-heading {
  color: #fff;
  font-family: 'Red Rose';
  text-transform: uppercase;
  font-weight: 800;
  transition-duration: 0.1s;

  text-shadow: 0 0 1px #fff, 0 0 3px #fff, 0 0 10px #fff, 0 0 10px #f8f1f1,
    0 0 0px #f8f1f1, 0 0 0px #f8f1f1, 0 0 0px #f8f1f1, 0 0 50px #f8f1f1;
}

.form-container {
  width: 60%;
  background-color: rgb(240, 232, 232);
  border-radius: 4px;
  padding: 6%;
  margin: auto;
}

.form-heading {
  font-size: 2.2rem;
  font-weight: 600;
  margin-bottom: 3%;
  color: #912031;
  letter-spacing: 1px;
  /* font-family: Cassandra, cursive; */
}

.rsvp-form-btn {
  border-radius: 30px;
  padding: 2% 4%;
  background-color: #912031;
  color: white;
  border: none;
}

.rsvp-form-btn:hover,
.rsvp-form-btn:focus {
  background-color: #50111b;
}

.form-msg {
  font-weight: bold;
  font-size: 1.75rem;
  font-family: cursive;
  color: #912031;
  margin-top: 7%;
  line-height: 2;
}

/* ------ Hashtag -------- */

.hashtag-section {
  background-image: url(/wedding-invitation-website/static/media/wood-bg1.0b590690.jpeg);
  background-size: cover;
  background-position: center center;
  padding: 2%;
  font-size: 5.5vmax;
  font-weight: 600;
  text-align: center;
  font-family: 'Red Rose', 'Montserrat', sans-serif;
  border: 10px solid black;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
