  /* box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px; */
}

/* ------------- MEDIA QUERIES ----------------------- */

@media screen and (max-width: 450px) {
  .main-section {
    background-image: url(/wedding-invitation-website/static/media/main-section-bg-mobile1.b664c1ff.jpg);
    background-position: center bottom !important;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .main-title {
    font-size: 4rem;
    top: 60%;
    /* color: red; */
  }

  .section-heading {
    font-size: 4rem !important;
  }
  .countdown-container {
    bottom: -2vh;
    width: 90%;
    left: 5%;
  }

  .invitation-pic {
    min-width: 100% !important;
    max-height: 50% !important;
  }

  .invitation-date {
    font-size: 2.8rem;
  }
  .assisting-text {
    padding-top: 5%;
  }

  .invitation-venue {
    margin-top: 5%;
  }

  .form-container {
    width: 90%;
    margin: 6% auto;
  }

  .form-heading {
    font-size: 1.8rem;
  }

  .form-msg {
    font-size: 1rem;
  }

  .rsvp-form-btn {
    padding: 3% 5%;
  }

  /* .venue-card {
    margin-bottom: 20px;
  } */
}

@media screen and (min-width: 450px) and (max-width: 650px) {
  .main-title {
    font-size: 5rem;
    top: 55%;
  }
  .countdown-container {
    bottom: -3vh;
    width: 80%;
    left: 10%;
  }
  .invitation-date {
    font-size: 2.8rem;
  }

  .section-heading {
    font-size: 5rem;
  }
  .form-container {
    width: 80%;
  }
}

@media screen and (min-width: 650px) and (max-width: 1050px) {
  .main-title {
    font-size: 6.2rem;
    top: 60%;
  }
  .invitation-date {
    font-size: 2.8rem;
  }

  .section-heading {
    font-size: 6.5rem;
  }
  .form-container {
    width: 70%;
  }

  .card-profile {
    width: 100%;
    min-height: 100% !important;
  }
}

@media (max-width: 767px) {
  .content-container {
    background-image: url(/wedding-invitation-website/static/media/invite-bg4.030593e0.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .family-section {
    padding-bottom: 20%;
  }

  .parents-bubble .bubble {
    margin-bottom: 6px;
  }

  .parents-pointer {
    top: calc(0.5vh - 5px);
  }

  .sibling-msg .bubble {
    margin-bottom: 0px;
  }

  .speech-bubble .col-5,
  .speech-bubble .col-sm-5,
  .speech-bubble .col-7,
  .speech-bubble .col-sm-7 {
    padding: 0 !important;
  }

  .speech-bubble .family-member-intro {
    text-align: left !important;
    margin-left: 5%;
  }

  .groom-sibling-pointer {
    border-radius: 0 0 12px 0;
    /* margin-top: -100px; */
    left: calc(0.5vw + 20%);
    top: calc(0.5vh - 25px);
    /* background-color: black; */
  }

  .bride-sibling-pointer {
    border-radius: 0 0 12px 0;
    /* margin-top: -100px; */
    left: calc(0.5vw - 80%);
    top: calc(0.5vh - 24px);
    /* background-color: black; */
  }

  .family-section .carousel-indicators {
    bottom: -20px !important;
  }
}

@media screen and (max-width: 768px) {
  .sub-heading {
    font-size: 1.8rem;
    text-align: center;
    margin-bottom: 20px;
  }

  .invitation-container .col-sm-12,
  .invitation-container .col-12 {
    padding: 0 !important;
  }

  .friend-name {
    text-align: center;
    margin-top: 6px;
    margin-bottom: 10px;
    font-size: 1.4rem;
  }

  .bridesmaid-heading {
    margin-top: 30px;
  }

  .friends-slider-container {
    padding-bottom: 50px;
  }
  .countdown-container {
    bottom: -3vh;
    width: 80%;
    left: 10%;
  }
}

@media (min-width: 768px) and (max-width: 992px) {
  .friends-slider-container {
    margin-left: 30px !important;
    margin-right: 30px !important;
    max-width: 92% !important;
    margin: auto;
  }

  .countdown-container {
    bottom: -3vh;
    width: 80%;
    left: 10%;
  }

  .friend-name {
    margin-left: 15%;
  }
}

@media (min-width: 768px) and (max-width: 1200px) {
  .sangeet-function-name {
    font-size: 1.2rem;
  }
}

@media (min-width: 992px) and (max-width: 1200px) {
  .sangeet-function-name {
    font-size: 1.2rem;
  }

  .countdown-container {
    width: 80%;
    bottom: -3vh;
    left: 10%;
  }
}
